"use client"

import styled from "styled-components";
import Link from "next/link";
import ThumbImg from "@/components/mypage/common(temporary)/ThumbImg";
import { MYSUMMARYNEWS } from "@/constants/mypageData";

interface Props {
  flex?: number;
  className?: string;
}

function PopularNewsletter({flex, className}: Props) {
  
  const mappedNewsletter = MYSUMMARYNEWS.map((news, i) => ({index: i, value: news}))
  const sortNewsletter = mappedNewsletter.sort((a, b) => b.value.viewcount - a.value.viewcount);
  const sliceNewsletter = sortNewsletter.slice(0, 5);
  
  return (
    <PopularNewsletterStyled flex={flex} className={className}>
      <h3 className="section-title">지금 인기 아티클 TOP 5</h3>
      <ul>
        {sliceNewsletter.map((news, index) => (
          <li key={index}>
            <p className="index">{index+1}</p>
            <div className="text-section">
              {/*<Link href="#"><p className="category">{news.value.categoryName}</p></Link>*/}
              <Link href={"#"}>
                <p className="title">{news.value.title}</p>
              </Link>
              <p className="date">{news.value.createdAt}</p>
            </div>
            {news.value.img &&
              <Link href={"#"} className="thum-link">
                <ThumbImg src={news.value.img} aspectratio={1} />
              </Link>
            }
          </li>
        ))}
      </ul>
    </PopularNewsletterStyled>
  );
}

const PopularNewsletterStyled = styled.div<Props>`
    position: sticky;
    overflow: auto;
    height: 100%;
    top: 1rem;
    
    .index {
        color: ${({theme}) => theme.color.subText};
    }


    .section-title {
        font-size: ${({theme}) => theme.fontSize.medium};
        font-weight: ${({theme}) => theme.fontWeight.regular};
        padding: 0 0 1rem 0;
        border-bottom: 1px solid ${({theme}) => theme.color.border};
        margin-bottom: 1rem;
    }

    ul {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        li {
            overflow: hidden;
            display: flex;
            flex-direction: row;
            gap: 1rem;

            .thum-link {
                flex: 1;
                height: fit-content;
                margin: 0;
            }

            .text-section {
                flex: 3;
                word-break: break-word;
                height: 4.25rem;


                .category {
                    font-size: ${({theme}) => theme.fontSize.extraSmall};
                    color: ${({theme}) => theme.color.primary};
                    font-weight: ${({theme}) => theme.fontWeight.medium};
                }

                .title {
                    font-size: ${({theme}) => theme.fontSize.small};
                    font-weight: ${({theme}) => theme.fontWeight.medium};

                    overflow: hidden;
                    text-overflow: ellipsis;

                    display: -webkit-box;
                    -webkit-line-clamp: 2; // 원하는 라인수
                    -webkit-box-orient: vertical
                }

                .date {
                    font-size: ${({theme}) => theme.fontSize.extraSmall};
                    color: ${({theme}) => theme.color.lightGrey};
                }
            }
        }
    }
`;

export default PopularNewsletter;